.gallery {
  margin-top: 25px;
  margin-bottom: 70px; }
  .gallery .event-description {
    max-width: 700px;
    margin-bottom: 10px; }
  .gallery .category {
    text-transform: uppercase; }
  .gallery a {
    text-decoration: none; }

.image-row-container {
  position: relative; }

.image-row {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between; }
  @media only screen and (max-width: 768px) {
    .image-row {
      justify-content: center; } }
  .image-row img {
    flex: 0 1 200px;
    max-width: 350px;
    height: auto; }

.facebook-link {
  top: -2em;
  right: 0;
  position: absolute; }
  @media only screen and (max-width: 768px) {
    .facebook-link {
      display: none; } }

.chevron {
  position: relative;
  display: inline-block;
  border-right: 0.05em solid rgba(0, 0, 0, 0.87);
  border-top: 0.05em solid rgba(0, 0, 0, 0.87);
  transform: rotate(45deg);
  width: .5em;
  height: .5em;
  margin-left: .1em; }
