.partner-panel {
  grid-area: partner-panel; }

.partner-info-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: 20px; }
  @media only screen and (max-width: 768px) {
    .partner-info-container {
      justify-content: center; } }

.partner-template-container {
  width: 280px;
  padding: 0 10px;
  flex-shrink: 0; }
  .partner-template-container .partner-title {
    margin-bottom: 8px; }
  .partner-template-container .partner-image-container {
    height: 125px;
    position: relative; }
    .partner-template-container .partner-image-container .partner-image {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 90%; }
