.banner-section {
  min-height: calc(100vh - 100px);
  grid-area: banner;
  display: flex;
  align-content: center; }
  @media only screen and (max-width: 1024px) {
    .banner-section {
      min-height: calc(100vh - 800px); } }
  .banner-section .banner-container {
    display: grid;
    align-items: center;
    grid-template-areas: "title image-ring";
    grid-template-columns: auto 40%; }
    .banner-section .banner-container > * {
      margin-top: -10vh; }
    @media only screen and (max-width: 1024px) {
      .banner-section .banner-container {
        grid-template-areas: "image-ring" "title";
        text-align: center;
        grid-template-columns: auto; }
        .banner-section .banner-container > * {
          margin-top: 0; } }
    .banner-section .banner-container .main-title {
      grid-area: title;
      margin-bottom: 10px;
      padding-top: 10px; }
    .banner-section .banner-container .banner-image-wheel {
      grid-area: image-ring;
      justify-self: center;
      width: 100%;
      padding-top: 100%;
      position: relative; }
      @media only screen and (max-width: 1024px) {
        .banner-section .banner-container .banner-image-wheel {
          max-width: 50vw; } }
      @media only screen and (max-width: 768px) {
        .banner-section .banner-container .banner-image-wheel {
          max-width: 70vw; } }
      .banner-section .banner-container .banner-image-wheel .banner-image {
        border-radius: 50%;
        border: #BABABA 3px solid;
        overflow: hidden;
        display: block;
        position: absolute;
        top: 50%;
        left: 50%; }
