.landing-page {
  display: grid;
  grid-template-rows: auto auto auto auto auto;
  grid-template-columns: 0 auto 0;
  grid-template-areas: ". banner ." ". values-panel ." ". gallery-panel ." ". partner-panel .";
  grid-row-gap: 100px; }
  @media only screen and (min-width: 969px) {
    .landing-page {
      margin-top: 72px; } }
  @media only screen and (min-width: 992px) {
    .landing-page {
      grid-template-columns: 15% auto 15%; } }
  @media only screen and (max-width: 768px) {
    .landing-page {
      grid-template-columns: 15px calc(100vw - 30px) 15px; } }
  @media only screen and (max-width: 768px) {
    .landing-page {
      grid-row-gap: 25px; } }
