.list-of-values {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  flex-direction: row; }

.value {
  display: flex;
  padding: 50px;
  flex-direction: row; }
  @media only screen and (min-width: 969px) {
    .value {
      max-width: 50%; } }
  @media only screen and (max-width: 768px) {
    .value {
      padding: 5px; } }
  @media only screen and (min-width: 1200px) {
    .value {
      padding: 25px; } }

.content {
  flex-direction: column;
  padding-left: 50px; }
  @media only screen and (max-width: 768px) {
    .content {
      padding-left: 20px; } }

.values-img {
  width: 200px;
  height: 200px;
  max-width: 30vw;
  max-height: 30vw;
  display: block;
  margin: auto auto; }
  @media only screen and (max-width: 375px) {
    .values-img {
      width: 95px;
      height: 95px; } }

.values-title {
  padding-top: 10px;
  width: 200px; }

.values-description {
  overflow-wrap: break-word;
  word-wrap: break-word;
  width: 200px; }
